.collapse-list {
	margin: 0 0 58px;
	opacity: 0;
	transition: opacity 0.2s;
	transition-delay: 0.2s;

	&_show {
		opacity: 1;
	}

	&_inner {
		.collapse-head {
			&__title {
				font-size: 24px;
				line-height: 35px;
			}
		}
	}

	&__content {
		position: relative;
		margin-left: -27px;
		padding-bottom: 2px;

		&::after {
			content: "";
			position: absolute;
			left: 92px;
			right: 0;
			bottom: 0;
			display: block;
			height: 2px;
			background-color: rgba($color-black, 0.3);
		}
	}
}

.collapse-item {
	position: relative;
}

.collapse-head {
	position: relative;
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	justify-content: flex-start;
	min-height: 90px;
	border-radius: 8px 8px 0 0;
	padding: 16px 27px;
	transition: background-color 0.2s, color 0.2s;
	cursor: pointer;

	&::after {
		content: "";
		position: absolute;
		left: 92px;
		right: 0;
		top: 0;
		display: block;
		height: 2px;
		background-color: rgba($color-black, 0.3);
		transition: opacity 0.2s;
	}

	&_active {
		color: $color-white;
		background-color: $color-razzmatazz;

		&::after {
			opacity: 0;
		}

		.collapse-head {
			&__icon {
				svg {
					fill: $color-white;
				}
			}

			&__arrow {
				.icon {
					transform: translateY(-50%) rotate(180deg);
				}

				svg {
					fill: $color-white;
				}
			}

			&__count {
				color: $color-white;
			}

			&__title {
				&::before {
					opacity: 0;
				}
			}
		}
	}

	&__icon {
		width: 65px;

		svg {
			fill: $color-razzmatazz;
		}
	}

	&__content {
		position: relative;
		display: flex;
		flex: 0 1 100%;
		flex-flow: row nowrap;
		align-items: center;
	}

	&__arrow {
		flex: 0 0 46px;

		.icon {
			position: absolute;
			right: 26px;
			top: 50%;
			fill: $color-razzmatazz;
			transform-origin: center center;
			transform: translateY(-50%);
			transition: transform 0.3s;
		}
	}

	&__count {
		flex: 0 0 160px;
		font-size: 16px;
		line-height: 25px;
		color: $color-Boulder;
		transition: color 0.2s;
	}

	&__title {
		position: relative;
		display: inline-block;
		font-weight: 700;
		font-size: 36px;
		line-height: 52px;

		&::before {
			content: "";
			position: absolute;
			left: 0;
			right: 0;
			bottom: 0;
			display: block;
			border-bottom: 1px dashed rgba(0, 0, 0, 0.4);
			transition: opacity 0.2s;
		}
	}
}

.collapse-content {
	position: relative;
	padding-right: 25px;
	padding-left: 25px;

	&::before {
		content: "";
		position: absolute;
		left: 92px;
		right: 0;
		bottom: -2px;
		z-index: 2;
		display: block;
		height: 2px;
		background-color: transparent;
		transition: left 0.15s, background-color 0.15s;
	}

	&.show {
		&::before {
			left: 0;
			background-color: $color-black;
		}
	}
}
