.material-item {
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	margin-right: 45px;
	margin-bottom: 35px;
	font-size: 16px;
	line-height: 25px;
	text-decoration: none;
	color: $color-black;

	&__icon {
		margin-right: 18px;
	}

	&__image {
		position: relative;
		flex: 0 0 56px;
		margin-right: 11px;
		width: 56px;
		height: 56px;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__title {
		text-decoration: underline;
		text-decoration-color: rgba($color-black, 0.2);
	}

	&__video-icon {
		position: absolute;
		left: 50%;
		top: 50%;
		flex: 0 0 56px;
		margin-right: 11px;
		width: 56px;
		height: 56px;
		transform: translate(-50%, -50%);

		&::before {
			content: "";
			position: absolute;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			z-index: 2;
			display: block;
			background-color: rgba(0, 0, 0, 0.31);
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}

		svg {
			fill: $color-white;
		}

		.icon {
			position: absolute;
			left: 50%;
			top: 50%;
			z-index: 3;
			transform: translate(-50%, -50%);
		}
	}

	&__media-icon {
		position: absolute;
		left: 50%;
		top: 50%;
		flex: 0 0 56px;
		border: 2px solid rgba(0, 0, 0, 0.5);
		width: 56px;
		height: 56px;
		transform: translate(-50%, -50%);
		border-radius: 2px;

		.icon {
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
		}

		svg {
			fill: $color-razzmatazz;
		}
	}
}
