.material-slider {
	display: flex;
	flex-flow: row nowrap;
	align-items: stretch;
	justify-content: stretch;
	min-height: 100px;

	&_not-slider {
		.material-slider {
			&__button {
				display: none;
			}

			&__wrapper {
				overflow: visible;

				.swiper-slide {
					flex: 0 0 28%;
				}
			}

			&__scrollbar {
				display: none;
			}
		}
	}

	&__button {
		position: absolute;
		top: 0;
		bottom: 0;
		z-index: 4;
		margin: 0;
		width: 45px;
		height: 100%;
		transform: none;
		transition: opacity 0.2s;
		cursor: pointer;

		&_prev {
			left: 0;
		}

		&_next {
			right: 0;
		}

		&_disabled {
			opacity: 0.5;
			pointer-events: none;
		}
	}

	&__icon {
		width: 28px;
		height: 100%;

		svg {
			fill: $color-razzmatazz;
		}

		.icon {
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
		}
	}

	&__container {
		flex: 0 0 100%;
		padding-right: 65px;
		padding-left: 65px;

		&::before,
		&::after {
			content: "";
			position: absolute;
			top: 0;
			bottom: 0;
			z-index: 3;
			display: block;
			width: 274px;
			height: 217px;
			opacity: 0;
			transition: opacity 0.2s;
			pointer-events: none;
		}

		&::before {
			left: -135px;
			background: linear-gradient(-270deg, $color-white 68.22%, rgba(255, 255, 255, 0) 100%);
		}

		&::after {
			right: -135px;
			background: linear-gradient(270deg, $color-white 68.22%, rgba(255, 255, 255, 0) 100%);
		}

		&_has-before {
			&::before {
				opacity: 1;
			}
		}

		&_has-after {
			&::after {
				opacity: 1;
			}
		}
	}

	&__wrapper {
		padding-top: 45px;
		padding-bottom: 45px;
	}
}
