.video-link {
	position: relative;
	overflow: hidden;
	display: block;
	border-radius: 8px;
	padding-top: 56.25%;
	width: 100%;
	height: 0;

	&__background {
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		z-index: 1;

		&::before {
			content: "";
			position: absolute;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			z-index: 2;
			display: block;
			background-color: rgba(0, 0, 0, 0.7);
		}

		img {
			position: relative;
			z-index: 1;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__title {
		position: absolute;
		left: 17px;
		right: 17px;
		top: 16px;
		z-index: 2;
		font-size: 16px;
		line-height: 25px;
		color: $color-white;

		span {
			border-bottom: 1px solid rgba($color-white, 0.2);
		}
	}

	&__icon {
		position: absolute;
		left: 50%;
		top: 50%;
		z-index: 2;
		transform: translate(-50%, -50%);

		svg {
			fill: $color-white;
		}
	}
}
