.content-card {
	position: relative;
	margin-bottom: 60px;
	padding-left: 64px;

	&__number {
		position: absolute;
		left: 0;
		top: -20px;
		font-weight: 700;
		font-size: 64px;
		line-height: 92px;
		color: $color-razzmatazz;
	}

	&__title {
		margin-bottom: 0;
		font-weight: 700;
		font-size: 36px;
		line-height: 52px;
	}

	&__text {
		font-size: 16px;
		line-height: 25px;
	}
}
