.cards {
	&__container {
		display: flex;
		flex-flow: row wrap;
		align-items: flex-start;
		min-width: 908px;
	}

	&__background-image {
		position: absolute;
		left: 0;
		bottom: 0;
		z-index: -1;
		overflow: hidden;
		width: 721px;
		height: 707px;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}
}
