.card {
	position: relative;
	display: block;
	flex: 0 0 50%;
	margin-bottom: -1px;
	margin-left: -1px;
	border: 1px solid $color-white;
	border-radius: 8px;
	padding: 48px 50px;
	width: 453px;
	height: 290px;
	text-decoration: none;
	color: $color-white;

	&__title {
		display: inline-block;
		border-bottom: 1px solid rgba($color-white, 0.25);
		font-weight: 700;
		font-size: 36px;
		line-height: 52px;
		letter-spacing: 0.3px;

		&_dotted-line {
			border-bottom: 1px dashed rgba($color-white, 0.25);
		}
	}

	&__arrow {
		position: absolute;
		left: 51px;
		bottom: 57px;
		border: 3px solid $color-white;
		border-radius: 50%;
		width: 46px;
		height: 46px;

		svg {
			position: absolute;
			left: 50%;
			top: 50%;
			width: 17px;
			height: 17px;
			fill: $color-white;
			transform: translate(-50%, -50%);
		}
	}

	&__icon {
		position: absolute;
		opacity: 0.1;

		svg {
			width: 100%;
			height: 100%;
			fill: $color-white;
		}

		&_about-archive {
			right: 54px;
			bottom: 60px;
			width: 63px;
			height: 77px;
		}

		&_publications {
			right: 38px;
			bottom: 57px;
			width: 86px;
			height: 86px;
		}

		&_expositions {
			right: 52px;
			bottom: 52px;
			width: 68px;
			height: 89px;
		}

		&_collections {
			right: 57px;
			bottom: 49px;
			width: 58px;
			height: 81px;
		}
	}

	&_1 {
		border-bottom-right-radius: 0;
		background: linear-gradient(113.92deg, $color-st-tropaz 100%, $color-razzmatazz 360.76%);
	}

	&_2 {
		border-bottom-left-radius: 0;
		background: linear-gradient(113.78deg, $color-st-tropaz 21.96%, $color-razzmatazz 283.66%);
	}

	&_3 {
		border-top-right-radius: 0;
		background: linear-gradient(113.89deg, $color-st-tropaz 78.03%, $color-razzmatazz 339.36%);
	}

	&_4 {
		border-top-left-radius: 0;
		background: linear-gradient(113.58deg, $color-st-tropaz 0%, $color-razzmatazz 262.69%);
	}
}
