.header {
	padding: 25px 40px 24px;
	height: 144px;

	&__btn {
		margin-top: 7px;
		margin-right: 72px;
		padding-right: 56px;
		text-decoration: none;

		&:last-of-type {
			margin-right: 0;
		}
	}

	&__icon {
		display: inline-block;
		vertical-align: bottom;
		margin-right: 12px;
		height: 41px;

		svg {
			fill: $color-razzmatazz;
		}
	}
}
