.main-page-lead {
	display: flex;
	flex-flow: row nowrap;
	align-items: stretch;
	min-height: calc(100vh - 144px);

	&__col {
		flex: 0 1 50%;

		&_left {
			position: relative;
			flex-basis: 1195px;
			padding-top: 89px;
			padding-right: 94px;
			padding-left: 196px;

			&::before {
				content: "";
				position: absolute;
				right: 0;
				top: 0;
				bottom: 130px;
				display: block;
				width: 1px;
				background-color: rgba(0, 0, 0, 0.3);
			}
		}

		&_right {
			flex: 0 1 100%;
		}
	}
}
