.logo {
	display: inline-block;
	vertical-align: middle;
	margin-right: 87px;
	font-family: $font-fira-sans;
	font-style: normal;
	font-size: 22px;
	line-height: 30px;
	color: $color-black;

	&:hover {
		.header__logo-image {
			transform: rotateY(360deg);
		}
	}

	&__image {
		display: inline-block;
		vertical-align: middle;
		margin-right: 32px;
		width: 95px;
		height: 100px;
		transform-origin: center center;
		transition: transform 1.5s;

		img {
			width: 100%;
			height: 100%;
			object-fit: contain;
		}
	}

	&__text {
		display: inline-block;
		vertical-align: middle;
		margin-top: 4px;
	}
}
