.news-card {
	position: relative;
	z-index: 1;
	margin-bottom: 50px;
	padding-top: 90px;
	padding-right: 147px;
	padding-left: 94px;

	&__today-in {
		position: absolute;
		right: 0;
		top: 0;
		z-index: -1;
		border-radius: 8px 0 0 8px;
		padding-top: 29px;
		padding-left: 32px;
		height: 156px;
		width: 80%;
		max-width: 413px;
		font-weight: 300;
		font-size: 24px;
		line-height: 35px;
		letter-spacing: 0.1px;
		color: $color-white;
		background: $color-razzmatazz;
	}

	&__image {
		overflow: hidden;
		margin-bottom: 62px;
		border-radius: 8px;
		width: 100%;
		height: 166px;

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__title {
		display: inline-block;
		margin-bottom: 21px;
		font-weight: 700;
		font-size: 24px;
		line-height: 35px;
		text-decoration: none;
		color: $color-black;

		span {
			border-bottom: 1px solid rgba($color-black, 0.25);
		}
	}

	&__content {
		margin-bottom: 49px;
		font-weight: 300;
		font-style: italic;
		font-size: 24px;
		line-height: 35px;
	}

	&__icon {
		display: inline-block;
		vertical-align: middle;
		margin-top: -4px;
		margin-right: 3px;
		margin-left: 25px;

		svg {
			fill: $color-razzmatazz;
		}
	}

	@media (max-width: 1800px) {
		padding-right: 50px;
		padding-left: 45px;
	}
}
