.show-menu {
	position: fixed;
	left: 0;
	top: 0;
	z-index: 50;

	&__btn {
		border: none;
		padding: 0;
		width: 144px;
		height: 144px;
		background: linear-gradient(99.26deg, $color-st-tropaz 14.02%, $color-razzmatazz 284.11%);
		transition: opacity 0.2s;

		.icon {
			position: absolute;
			left: 50%;
			top: 50%;
			transform: translate(-50%, -50%);
		}

		&:focus,
		&:active {
			opacity: 0.9;
		}

		svg {
			width: 100%;
			height: 100%;
			fill: $color-white;
		}

		&_show {
			border-radius: 0 0 8px;
		}

		&_back {
			position: fixed;
			left: 0;
			bottom: 0;
			border-radius: 0 8px 0 0;
		}

		&:focus,
		&:active {
			outline: none;
		}
	}

	&__text {
		position: absolute;
		left: 100%;
		top: 50%;
		margin-top: -12px;
		margin-left: 34px;
		height: 24px;
		font-size: 16px;
		line-height: 24px;
		pointer-events: none;
	}
}
