.fancy {
	position: absolute;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;

	&__inner {
		position: absolute;
		left: 0;
		top: 18px;
		bottom: 144px;
		display: flex;
		flex-flow: row nowrap;
		align-items: stretch;
		justify-content: flex-start;
		width: 1820px;

		.fancybox-content {
			background: transparent !important;
		}
	}

	&__video {
		display: flex;
		flex-flow: column nowrap;
		align-items: center;
		justify-content: center;
		margin: 0 auto;
		width: 100%;
		height: 100%;
		max-width: 1039px;
	}

	&__thumbs {
		position: absolute;
		left: 0;
		right: 0;
		bottom: 19px;
		height: 90px;

		.fancybox-thumbs {
			position: relative;
			width: 100%;
			overflow: hidden;
			white-space: nowrap;
			background-color: transparent;

			&__list {
				display: flex;
				flex-flow: row nowrap;
				align-items: stretch;
				justify-content: center;
				margin: 0 auto;
				width: 100%;
				height: 90px;
			}

			a {
				flex: 0 0 134px;
				margin-right: 16px;
				background-size: cover;

				&::before {
					border: 2px solid $color-razzmatazz;
				}
			}
		}
	}

	&__stage {
		z-index: 2;
	}

	&__arrow {
		position: absolute;
		top: 50%;
		z-index: 10;
		display: block;
		width: 90px;
		height: 90px;
		transform: translateY(-50%);
		transition: background-color 0.2s;

		svg {
			position: absolute;
			left: 50%;
			top: 50%;
			width: 22px;
			height: 41px;
			transform: translate(-50%, -50%);
		}

		&_left {
			left: 30px;
		}

		&_right {
			right: 30px;
		}

		&:hover,
		&:focus,
		&:active {
			outline: none;
			background-color: rgba($color-black, 0.3);
		}
	}

	&__content {
		position: relative;
		flex: 0 0 1443px;
		padding-right: 30px;
		padding-left: 30px;
		width: 1413px;
	}

	&__caption {
		position: relative;
		flex: 0 0 377px;
		padding: 40px 48px 40px 23px;
		width: 377px;
		font-size: 16px;
		line-height: 25px;
		background: $color-white;

		.fancybox-caption {
			position: static;
			margin: 0;
			padding: 0;
			color: $color-black;
			background: transparent;
			overflow: hidden;
		}
	}

	&__toolbar {
		position: absolute;
		right: 20px;
		top: 6px;
		z-index: 10;

		.fancy__btn {
			position: relative;
			width: 60px;
			height: 60px;

			svg {
				position: absolute;
				left: 50%;
				top: 50%;
				width: 40px;
				height: 40px;
				transform: translate(-50%, -50%);
			}
		}
	}

	&__infobar {
		position: absolute;
		left: 33px;
		top: 38px;
		z-index: 10000;
		font-weight: 300;
		font-size: 24px;
		line-height: 35px;
		color: $color-white;
	}

	&__btn {
		display: block;
		border: none;
		padding: 0;
		background-color: transparent;
		transition: opacity 0.2s, background-color 0.2s;
		cursor: pointer;

		&[disabled] {
			opacity: 0.5;
		}

		svg {
			fill: $color-white;
		}

		&:hover,
		&:focus,
		&:active {
			outline: none;
		}

		&:active {
			background-color: rgba($color-black, 0.3);
		}
	}

	&__close-btn {
		position: absolute;
		right: 30px;
		top: 36px;
		z-index: 3;

		svg {
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}
	}
}
