.titled-figure {
	&__image {
		position: relative;
		overflow: hidden;
		display: block;
		margin-bottom: 18px;
		padding-top: 66.72297%;
		width: 100%;
		height: 0;

		img {
			position: absolute;
			left: 0;
			top: 0;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__figcaption {
		font-weight: 300;
		font-style: italic;
		font-size: 16px;
		line-height: 25px;
	}
}
