.list-link {
	position: relative;
	display: flex;
	flex-flow: row nowrap;
	align-items: center;
	justify-content: flex-start;
	padding: 29px 70px 26px;
	text-decoration: none;
	color: $color-black;
	margin-left: -25px;
	margin-right: -25px;

	&::before {
		content: "";
		position: absolute;
		left: 92px;
		right: 0;
		bottom: 0;
		display: block;
		height: 2px;
		background-color: rgba($color-black, 0.3);
		transform: translateZ(0);
		transition: left 0.2s, background-color 0.2s;
	}

	&:active,
	&:focus {
		&::before {
			left: 0;
			background-color: $color-black;
		}
	}

	&:last-of-type {
		&::before {
			display: none;
		}
	}

	&__icon {
		flex: 0 0 66px;
	}

	&__type-count {
		flex: 0 0 115px;
		font-size: 16px;
		line-height: 25px;
		color: $color-Boulder;
		transition: color 0.2s;
	}

	&__title {
		border-bottom: 1px solid rgba($color-black, 0.4);
		font-weight: 700;
		font-size: 24px;
		line-height: 35px;
	}

	&:focus,
	&:active {
		outline: none;
		background-color: #f2f2f2;
	}
}
