.presentation-lead {
	position: relative;
	z-index: 1;
	padding-top: 66px;
	padding-left: 155px;

	&_has-bg {
		overflow: hidden;
		margin-bottom: -25px;
		min-height: 446px;
		color: $color-white;
	}

	&__bg {
		position: fixed;
		left: 0;
		right: 0;
		top: 0;
		z-index: -1;
		height: 100%;

		&::before {
			content: "";
			position: absolute;
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			z-index: 2;
			display: block;
			background: rgba(46, 5, 0, 0.4);
		}

		img {
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__title {
		margin-bottom: 50px;
		font-weight: 700;
		font-size: 64px;
		line-height: 92px;
	}

	&__content {
		font-weight: 300;
		font-style: italic;
		font-size: 24px;
		line-height: 35px;
	}

	&__btn {
		margin-bottom: 60px;
	}

	&__icon {
		margin-left: 10px;

		svg {
			fill: $color-white;
		}
	}
}
