.main-menu {
	position: fixed;
	left: 0;
	top: 0;
	bottom: 0;
	z-index: 100;
	border-radius: 0 8px 8px 0;
	width: 81.71875%;
	min-width: 1569px;
	background: linear-gradient(128.24deg, $color-st-tropaz 36.16%, $color-razzmatazz 202.72%);
	transform: translateX(-100%);
	transition: transform 0.45s;

	&_active {
		transform: translateX(0);

		.main-menu__overlay {
			display: block;
		}
	}

	&__overlay {
		position: absolute;
		left: 0;
		top: 0;
		bottom: 0;
		z-index: -1;
		display: none;
		width: 100vw;
		cursor: pointer;
	}

	&__container {
		display: flex;
		flex-flow: column nowrap;
		height: 100vh;
	}

	&__title {
		flex: 0 0 144px;
		border-bottom: 1px solid rgba($color-white, 0.3);
		height: 144px;
	}

	&__content {
		position: relative;
		display: flex;
		flex: 0 1 100%;
		flex-flow: row nowrap;
		align-items: stretch;
		justify-content: flex-start;
		overflow: hidden;
		padding-top: 2px;
		padding-left: 398px;
		transition: padding-left 0.3s;

		&_active {
			padding-left: 58px;
		}
	}

	&__section {
		position: relative;
		flex: 0 0 470px;
		overflow: hidden;
		height: 100%;

		&_inner {
			border-left: 1px solid rgba($color-white, 0.3);
			box-shadow: -41px 0 41px 0 rgba($color-black, 0.17);
			opacity: 0;
			transform: translateX(20%);
			transition: opacity 0.2s, transform 0.25s;
			pointer-events: none;
		}

		&_first {
			flex-basis: 420px;
		}

		&_second {
			flex-basis: 467px;
		}

		&_last {
			flex-basis: 469px;
		}

		&_active {
			opacity: 1;
			transform: translateX(0);
			pointer-events: auto;
		}
	}

	&__close-btn {
		position: relative;
		display: inline-block;
		vertical-align: middle;
		border: none;
		width: 144px;
		height: 144px;
		background-color: $color-razzmatazz;

		svg {
			position: absolute;
			left: 50%;
			top: 50%;
			margin-top: -18px;
			margin-left: -18px;
			width: 36px;
			height: 36px;
			fill: $color-white;
		}

		&:hover,
		&:focus,
		&:active {
			outline: none;
		}
	}

	&__close-text {
		display: inline-block;
		vertical-align: middle;
		margin-right: 225px;
		margin-left: 22px;
		font-size: 16px;
		line-height: 25px;
		color: $color-white;
	}

	&__buttons {
		display: inline-block;
		vertical-align: middle;
	}

	&__btn {
		margin-top: -2px;
		margin-right: 72px;
		padding-right: 56px;

		svg {
			fill: $color-white;
		}

		&:hover {
			svg {
				fill: $color-razzmatazz;
			}
		}
	}

	&__icon {
		display: inline-block;
		vertical-align: bottom;
		margin-right: 12px;
		height: 41px;

		svg {
			width: 100%;
			height: 100%;
		}

		&_question {
			width: 32px;
		}

		&_eye {
			width: 43px;
		}
	}
}
