.standstill {
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	z-index: 1000;
	display: none;
	color: $color-white;
	background: rgba(0, 0, 0, 0.9);

	&__content {
		margin: 325px auto 0;
		padding-left: 68px;
		width: 618px;
	}

	&__title {
		position: relative;
		margin-bottom: 16px;
		font-weight: 700;
		font-size: 36px;
		line-height: 52px;
	}

	&__text {
		font-size: 22px;
		line-height: 26px;

		span {
			color: $color-razzmatazz;
		}
	}

	&__last {
		position: relative;
		margin-top: 115px;
		font-weight: 300;
		font-size: 24px;
		line-height: 35px;
	}

	&__icon {
		position: absolute;
		left: -68px;
		top: 50%;
		transform: translateY(-50%);
	}
}
