.video {
	position: relative;
	overflow: hidden;
	border-radius: 8px;
	border: 1px solid $color-mine-shaft-lighter;
	box-sizing: border-box;
	background: linear-gradient(0deg, rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7));

	.vjs-big-play-button {
		position: absolute;
		left: 50%;
		top: 50%;
		border: none;
		width: 70px;
		height: 70px;
		background-color: transparent !important;
		transform: translate(-50%, -50%);
		transition: opacity 0.2s;

		&:hover,
		&:focus,
		&:active {
			outline: none;
			background: transparent !important;
			opacity: 0.6;
		}

		.vjs-icon-placeholder {
			&::before {
				font-size: 70px;
			}
		}
	}

	.vjs-button {
		> .vjs-icon-placeholder::before {
			position: absolute;
			left: 50%;
			top: 50%;
			font-size: 30px;
			line-height: 30px;
			transform: translate(-50%, -50%);
		}
	}

	.vjs-control {
		&:focus {
			text-shadow: none;
		}
	}

	.vjs-control-bar {
		left: 20px;
		right: 20px;
		bottom: 22px;
		display: flex;
		flex-flow: row nowrap;
		align-items: center;
		justify-content: space-between;
		border-radius: 78px;
		padding-right: 32px;
		padding-left: 32px;
		width: auto;
		height: 74px;
		background-color: $color-mine-shaft;

		.vjs-control {
			margin-right: 30px;
			width: 30px;
			height: 30px;

			&:last-of-type {
				margin-right: 0;
			}
		}
	}

	.vjs-slider {
		border-radius: 71px;
		background-color: rgba($color-white, 0.5);

		&:hover,
		&:focus {
			outline: none;
			box-shadow: none !important;
			text-shadow: none;
		}
	}

	.vjs-remaining-time {
		padding: 0;
		width: auto;
		font-size: 16px;
		line-height: 30px;
	}

	.vjs-progress-control {
		.vjs-time-tooltip {
			font-size: 12px !important;
		}

		&:hover {
			.vjs-time-tooltip {
				font-size: 12px !important;
			}

			.vjs-progress-holder {
				font-size: 10px;
			}
		}
	}

	.vjs-progress-holder {
		height: 6px;
	}

	.vjs-volume-bar.vjs-slider-horizontal {
		height: 6px;
		background-color: $color-white;

		.vjs-volume-level {
			height: 6px;
			background-color: $color-razzmatazz;

			&::before {
				position: absolute;
				right: -5px;
				top: 50%;
				display: block;
				border-radius: 50%;
				width: 16px;
				height: 16px;
				font-size: 0;
				line-height: 0;
				background-color: $color-white;
				transform: translateY(-50%);
			}
		}
	}

	.vjs-load-progress {
		background-color: $color-white;

		div {
			opacity: 0;
		}

		&:focus {
			outline: none;
		}
	}

	.vjs-play-progress {
		background-color: $color-razzmatazz;

		&::before {
			position: absolute;
			right: -20px;
			top: 50%;
			display: block;
			border-radius: 50%;
			width: 20px;
			height: 20px;
			font-size: 0;
			line-height: 0;
			background-color: $color-white;
			transform: translateY(-50%);
		}
	}

	&_audio {
		overflow: visible;
		padding-top: 0 !important;
		height: 74px !important;
		max-width: 903px;
		color: $color-razzmatazz;
		border: none;
		background: transparent;

		&.vjs-has-started.vjs-user-inactive.vjs-playing {
			.vjs-control-bar {
				opacity: 1;
			}
		}

		&.vjs-controls-disabled {
			.vjs-control-bar {
				display: flex !important;
			}
		}

		.vjs-fullscreen-control {
			display: none;
		}

		.vjs-poster {
			display: none;
		}

		.vjs-tech {
			display: none;
			background-color: transparent;
		}

		.vjs-big-play-button {
			display: none;
		}

		.vjs-play-control {
			order: 0;
		}

		.vjs-remaining-time {
			order: 1;
			color: $color-black;
		}

		.vjs-progress-control {
			order: 2;
		}

		.vjs-volume-panel {
			order: 3;
		}

		.vjs-control-bar {
			left: 0;
			right: 0;
			top: 0;
			bottom: 0;
			border: 2px solid #808080;
			background-color: $color-white;
		}

		.vjs-slider {
			background-color: #808080;
		}

		.vjs-play-progress {
			background-color: $color-razzmatazz;

			&::before {
				background-color: $color-razzmatazz;
			}
		}

		.vjs-volume-bar.vjs-slider-horizontal {
			background-color: #808080;

			.vjs-volume-level {
				&::before {
					background-color: $color-razzmatazz;
				}
			}
		}

		&.vjs_video_3-dimensions,
		&.vjs_video_581-dimensions {
			padding-top: 0 !important;
			height: 74px;

			&.vjs-fluid {
				padding-top: 0 !important;
				height: 74px;
			}
		}
	}

	&_small {
		.vjs-control-bar {
			height: 45px;
		}
	}

	&_medium {
		.vjs-control-bar {
			height: 55px;
		}
	}
}
