.bootstrap-select {
	display: block;
	width: 100%;

	&:not([class*="col-"]) {
		&:not([class*="form-control"]) {
			&:not(.input-group-btn) {
				width: 100%;
			}
		}
	}

	.btn {
		border: 2px solid $color-black !important;
		background: $color-white !important;
		padding-left: 40px;

		&:hover,
		&:focus,
		&:active {
			outline: none !important;
		}

		&::after {
			border: none;
			width: 18px;
			height: 18px;
			background: url("../images/sprites/svg/icon-arrow-down-pink.svg") no-repeat center center;
			background-size: contain;
		}
	}

	.dropdown-menu {
		top: 2px !important;
		z-index: -1;
		margin: 0;
		border-radius: 33px;
		padding: 100px 0 31px;
		box-shadow: 0 19px 19px -2px rgba($color-st-tropaz, 0.2);
		background: linear-gradient(101.06deg, $color-st-tropaz 14.02%, $color-razzmatazz 284.11%);
		transform: none !important;
	}

	.dropdown-item {
		position: relative;
		padding: 19px 72px;
		color: $color-white;

		&::before {
			content: "";
			position: absolute;
			left: 40px;
			top: 50%;
			display: block;
			width: 6px;
			height: 6px;
			margin-top: -3px;
			border-radius: 50%;
			background-color: $color-white;
			opacity: 0;
			transition: opacity 0.2s;
		}

		&.active,
		&.selected,
		&:hover {
			background-color: transparent;
		}

		&.selected {
			&::before {
				opacity: 1;
			}
		}
	}
}
