// stylelint-disable scss/dollar-variable-empty-line-before

// COLORS

$color-black: #000;
$color-white: #fff;
$color-razzmatazz: #f50057;
$color-st-tropaz: #27509e;
$color-mine-shaft: #272727;
$color-mine-shaft-lighter: #313131;

// FONTS
$font-fira-sans: "Fira Sans", sans-serif;

// Bootstrap variables

// BUTTONS
$btn-padding-y: 11px;
$btn-padding-x: 23px;
$btn-border-width: 2px;
$btn-border-radius: 97px;
$btn-font-size: 16px;
$btn-line-height: 41px;
$btn-box-shadow: none !important;
$btn-focus-box-shadow: none !important;

$primary: $color-razzmatazz;
$light: $color-white;
$dark: $color-black;
$color-Boulder: #777;

$h1-font-size: 64px;
$h2-font-size: 36px;
$h3-font-size: 24px;
$h1-line-height: 92px;
$h2-line-height: 52px;
$h3-line-height: 35px;
$headings-font-weight: 700;

$container-max-widths:
	(
		sm: 540px,
		md: 720px,
		lg: 960px,
		xl: 1404px
	) !default;
