.content-area {
	margin: 0 auto 160px;
	width: 100%;
	max-width: 1404px;
	font-size: 16px;
	line-height: 25px;

	&__announce {
		margin-bottom: 50px;
		font-weight: 300;
		font-style: italic;
		font-size: 24px;
		line-height: 35px;
	}

	&__offset-helper {
		margin-left: 30px;
	}

	&__number {
		font-weight: 700;
		font-size: 64px;
		line-height: 92px;
		color: $color-razzmatazz;
	}

	&__custom-list {
		li {
			&::before {
				color: $color-razzmatazz;
			}
		}
	}

	&__col {
		flex: 0 0 622px;
		padding: 0 15px;
		width: 622px;
	}

	&__links {
		margin-top: 30px;
	}

	@for $i from 1 through 6 {
		h#{$i} {
			margin: 0;
			font-weight: 700;
		}
	}

	h1 {
		margin-bottom: 65px;
		line-height: $h1-line-height;
	}

	h2 {
		margin-bottom: 30px;
		line-height: $h2-line-height;
	}

	h3 {
		margin-bottom: 30px;
		line-height: $h3-line-height;
	}

	p {
		margin: 0 0 15px;

		&:last-of-type {
			margin-bottom: 0;
		}
	}

	ul,
	ol {
		list-style: none;
		margin: 0;
		padding: 0;

		li {
			position: relative;
			margin-bottom: 30px;
			padding-left: 33px;

			&::before {
				content: "";
				position: absolute;
				left: 0;
			}
		}
	}

	ul {
		li {
			&::before {
				top: 12px;
				display: block;
				width: 20px;
				height: 1px;
				background-color: $color-black;
			}

			&:last-of-type {
				margin-bottom: 0;
			}
		}
	}

	ol {
		counter-reset: list-counter;

		li {
			counter-increment: list-counter;

			&::before {
				content: counter(list-counter) ".";
				top: 0;
				display: inline;
				font-weight: 700;
				font-size: 16px;
				line-height: inherit;
			}
		}
	}
}
