.ps {
	.ps__rail-y {
		right: 12px !important;
		width: 9px;
		background-color: transparent;

		&.ps--clicking {
			background-color: transparent;
		}

		&:hover {
			background-color: transparent;

			> .ps__thumb-y {
				background-color: rgba($color-black, 0.4);
				opacity: 1;
			}
		}
	}

	.ps__rail-x {
		&.ps--clicking {
			background-color: transparent;
		}

		&:hover {
			background-color: transparent;

			> .ps__thumb-x {
				background-color: rgba($color-black, 0.4);
				opacity: 1;
			}
		}
	}

	.ps__thumb-x,
	.ps__thumb-y {
		border-radius: 8px;
		background-color: rgba($color-black, 0.15);
		opacity: 1;
	}

	.ps__thumb-x {
		height: 9px;
	}

	.ps__thumb-y {
		width: 9px;
	}

	&--active-y {
		.ps__rail-y {
			> .ps__thumb-y {
				background-color: rgba($color-black, 0.4);
				opacity: 1;
			}
		}
	}

	&--active-x {
		.ps__rail-x {
			> .ps__thumb-x {
				background-color: rgba($color-black, 0.4);
				opacity: 1;
			}
		}
	}
}
