.btn {
	box-shadow: 0 9px 17px -6px rgba($color-st-tropaz, 0.15);
	font-size: $btn-font-size;
	text-decoration: none;
	box-sizing: border-box;

	&-primary {
		border-color: $color-white;

		&:hover,
		&:focus,
		&:active,
		&:not(:disabled):not(.disabled):active {
			border-color: $color-white;
		}
	}

	&_big {
		padding-left: 40px;
		font-weight: 300;
		font-size: 24px;
	}
}
