.menu-list {
	position: relative;
	list-style: none;
	margin: 0;
	padding: 100px 0 0;
	width: 100%;
	height: 100%;

	&_inner {
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		display: none;
		opacity: 0;
		transform: translateX(20%);
		transition: opacity 0.3s, transform 0.2s;
		pointer-events: none;

		.menu-list__btn {
			border-radius: 0;
			padding: 24px 50px 28px 92px;

			&::before {
				left: 92px;
			}
		}

		.menu-list__item {
			&:last-of-type {
				.menu-list__btn {
					&::after {
						left: 92px;
					}
				}
			}
		}
	}

	&_active {
		display: block;
		opacity: 1;
		transform: translateX(0);
		pointer-events: auto;
	}

	&__item {
		&:last-of-type {
			.menu-list__btn {
				&::after {
					content: "";
					position: absolute;
					left: 45px;
					right: 11px;
					bottom: 0;
					display: block;
					height: 1px;
					background-color: rgba($color-white, 0.3);
				}
			}
		}
	}

	&__btn {
		position: relative;
		display: block;
		outline: none;
		border-radius: 71px 0 0 71px;
		padding: 23px 50px 29px 45px;
		font-weight: 300;
		font-size: 24px;
		line-height: 35px;
		text-decoration: none;
		color: $color-white;
		transition: color 0.2s, background-color 0.2s;

		&::before {
			content: "";
			position: absolute;
			left: 45px;
			right: 11px;
			top: 0;
			display: block;
			height: 1px;
			background-color: rgba($color-white, 0.3);
			transition: opacity 0.2s;
		}

		&:focus,
		&:hover,
		&:active {
			outline: none;
		}

		&_active,
		&:active {
			color: $color-black;
			background-color: $color-white;

			.menu-list__icon {
				svg {
					fill: $color-black;
				}
			}
		}
	}

	&__icon {
		position: absolute;
		right: 29px;
		top: 50%;
		z-index: 2;
		margin-top: -7px;
		width: 14px;
		height: 14px;
		pointer-events: none;

		svg {
			display: block;
			width: 100%;
			height: 100%;
			fill: $color-white;
			transition: fill 0.2s;
		}
	}
}
