.image-card {
	position: relative;
	padding: 65px 190px 65px 156px;
	width: 100%;
	min-height: 812px;
	color: $color-white;

	&__background {
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		z-index: 1;

		&::before {
			content: "";
			position: absolute;
			left: 0;
			top: 0;
			z-index: 2;
			width: 100%;
			height: 100%;
			background: linear-gradient(0deg, rgba(46, 5, 0, 0.4), rgba(46, 5, 0, 0.4));
		}

		img {
			position: relative;
			z-index: 1;
			width: 100%;
			height: 100%;
			object-fit: cover;
		}
	}

	&__content {
		position: relative;
		z-index: 2;
		font-weight: 300;
		font-style: italic;
		font-size: 24px;
		line-height: 35px;
	}

	&__title {
		margin-bottom: 55px;
		font-weight: 700;
		font-style: normal;
		font-size: 64px;
		line-height: 92px;
	}
}
