.material-card {
	margin-bottom: 60px;

	&__number {
		font-weight: 700;
		font-size: 64px;
		line-height: 92px;
		color: $color-razzmatazz;
	}

	&__title {
		margin-bottom: 30px;
		font-weight: 700;
		font-size: 24px;
		line-height: 35px;
	}

	&_inline {
		position: relative;
		padding-left: 64px;

		.material-card {
			&__number {
				position: absolute;
				left: 0;
				top: -20px;
			}
		}
	}

	&:last-of-type {
		margin-bottom: 0;
	}
}
