.table {
	width: 100%;

	thead {
		color: $color-white;
		background-color: $color-razzmatazz;
	}

	tr {
		padding-right: 10px;
		padding-left: 10px;
	}

	tbody {
		tr {
			border-bottom: 1px solid $color-black;
		}
	}

	th {
		font-weight: 300;
	}

	td, th {
		padding: 39px 20px 42px;
		text-align: left;

		&.table__text-right {
			text-align: right;
		}

		&.table__nowrap {
			white-space: nowrap;
		}

		&.table__big {
			padding: 49px 20px 52px;
			text-align: center;
		}
	}
}
